@font-face {
  font-family: 'MusticaProSemiBold';
  src: local('MusticaProSemiBold'), url(../fonts/MusticaProSemiBold.otf) format('opentype');
}
@font-face {
  font-family: 'Roadway';
  src: local('Roadway'), url(../fonts/Roadway.ttf) format('truetype');
}
@font-face {
  font-family: 'DINMediumRegular';
  src: local('DINMediumRegular'), url(../fonts/DINMediumRegular.ttf) format('truetype');
}

body {
  background-color: rgb(39, 39, 39);
  margin: 0 auto;
  transform-origin: top;
}

.section-container {
  position: absolute;
  width: 100%;
  top: 0;
  
  display: flex;
  flex-direction: column;
  align-items: center;

}

.section-container > * {
  width: 80%;
}

h2 {
  font-size: 50pt;
  margin: 0pt;
  font-family: 'MusticaProSemiBold';
  color: white;
}

.subtle-font {
  font-size: 20pt;
  font-family: 'DINMediumRegular';
  color: rgb(190, 190, 190);
}

/* -------------------------- ABOUT SECTION -------------------------- */

#about-section {
  display: flex;
  flex-direction: column;
}

/* -------------------------- EXPERIENCE SECTION -------------------------- */

#experience-section {
  display: flex;
  flex-direction: column;
}

/* -------------------------- PROJECTS SECTION -------------------------- */

#projects-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  row-gap: 25px;
}

.project-list{
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;

  max-width: 1600px;
}
